@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
.Card {
    background-image: url(/poke-app/static/media/d3n22fk-ca665f8a-9aa5-4508-9132-a0cc7b06c3b8.1beb29e7.png);
    border-radius: 10px;
    border: 5px solid #0000CD;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Card__img {
    width: 200px;
    height:200px;
}

.Card__name {
    font-size: 20px;
    text-transform: capitalize;
    font-weight: 700;
}

.Card__types {
    margin-top: 20px;
    display: flex;
}
.Card__type {
    padding: 2px;
    margin-right: 20px;
    background-color: #38ac85;
    color: #ffffff;
    border-radius: 5px;

}

.Card__info {
    margin-top: 30px;
    display: flex;
}
.Card__data {
    margin-right: 30px;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  background: rgb(50,129,167);
  background: linear-gradient(90deg, rgba(50,129,167,1) 0%, rgba(24,189,184,1) 9%, rgba(255,0,0,1) 60%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#3281a7",endColorstr="#ff0000",GradientType=1);
  font-family: 'Poppins', sans-serif;}

a {
  text-decoration: none;
}

.container {
  max-width: 1440px;
  margin: auto;
  padding: 0 20px;
}

.btn__container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.btn {
  cursor: pointer;
  color: #ffffff;
  background-color: #133cc2;
  font-size: 1em;
  text-align: center;
  padding: 10px 15px;
  border: 0;
  margin-right: 30px;
  border-radius: 5px;
}

.btn__bottom {
  margin-top: 20px;
}

header {
  height: 350px;
}

header img {
  width: 350px;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.search {
  height: 100px;
}

input[type='text'] {
  display: block;
  padding: 10px;
  font-size: 20px;
  border: 0;
  border-radius: 5px;
  width: 60%;
  margin: auto;
  outline: none;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

@media (max-width: 800px){
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 500px) {
  .grid-container{
    grid-template-columns: 1fr;
  }
  .card img {
    width: 100%;
    height: 300px;
    object-fit: contain;
  }
}
